<template>
	<div>
		<div class="map">
			<div class="item">
				<el-input id="latLng" v-model="latlng" placeholder="" :disabled="true"></el-input>
			</div>
			<div class="item">
				<el-cascader style="width: 300px;"  v-model="mapvalue" placeholder="" :options="mapData" filterable :props="props" @change="handleMapValue"></el-cascader>
			</div>
			<div class="item">
				<el-input v-model="address" placeholder=""  @focus="handleAddress" class="search" @keyup.enter.native="search(address)"></el-input>
			</div>
			<div class="item btn">
				<el-button type="primary" @click="search(address)"></el-button>
			</div>
		</div>
		<div id="container">

		</div>
	</div>
</template>

<script>
	import { map } from './map-data.js'
	export default {
		data() {
			return {
				latLng: {},
				latlng: '22.508949,113.321297', // 
				address: '', // 
				getAddress: null,
				getAddCode: null,
				map: null,
				marker: null,
				mapvalue: '',
				mapData: map,// 
				props: {
					label: 'label',
					value: 'label'
				}
			}
		},
		mounted() {
			this.init();
		},
		methods: {
			handleAddress() {
				var self = this;
				self.address = '';
				if(!self.mapvalue) {
					self.$message.error("");
				}
			},
			search() {
				var self = this;
				//getLocation();
				self.getAddress.getLocation(self.mapvalue + self.address);
			},
			handleMapValue() {
				this.search();
				this.address = '';
			},
			// 
			getAddressCode() {
				var lat = parseFloat(this.latLng.lat);
				var lng = parseFloat(this.latLng.lng);
				var latLng = new qq.maps.LatLng(lat, lng);
				//
				this.getAddCode.getAddress(latLng);
			},
			init() {
				var self = this;
				var center = new qq.maps.LatLng(22.51595, 113.3926);
				self.map = new qq.maps.Map(document.getElementById('container'), {
					center: center,
					zoom: 15,
				});

				//Marker
				self.marker = new qq.maps.Marker({
					//Marker
					position: center,
					//Marker
					map: self.map
				});
				

				// 
				qq.maps.event.addListener(self.map, 'click', function(e) {
					self.latLng.lat = e.latLng.getLat();
					self.latLng.lng = e.latLng.getLng();
					self.latlng = `${self.latLng.lat},${self.latLng.lng}`;
					self.getAddressCode();
				});

				//
				self.getAddCode = new qq.maps.Geocoder({
					complete: function(result) {
						self.marker.setMap(null)
						self.address = result.detail.address;
						self.mapvalue = '';
						self.marker = new qq.maps.Marker({
							map: self.map,
							position: result.detail.location
						});
						
					}
				});

				self.getAddCode.setError(function() {
					self.$message.error("");
				});

				//
				self.getAddress = new qq.maps.Geocoder({
					complete: function(result) {
						self.marker.setMap(null)
						self.map.setCenter(result.detail.location);
						console.log(result.detail.location)
						self.latLng.lng = result.detail.location.lng;
						self.latLng.lat = result.detail.location.lat;
						self.latlng = `${self.latLng.lat},${self.latLng.lng}`;
						self.marker = new qq.maps.Marker({
							map: self.map,
							position: result.detail.location
						});
					}
				});

				self.getAddress.setError(function() {
					self.$message.error("");
				});
			}

		}
	}
</script>

<style scoped lang="scss">
	.map {
		margin-bottom: 10px;

		.item {
			display: inline-block;
			width: 300px;
			margin: 10px 5px;
		}
	}

	#container {
		width: 62.5rem;
		height: 40rem;
		border: solid 1px #ccc;
	}
</style>
